"use client";

import React, { useEffect, useRef } from "react";
import { useDispatch, useSelector } from "react-redux";
import { AppDispatch, RootState } from "@/redux/app/store";
import { fetchServices } from "@/redux/features/ServicesSlice";
import { fetchBarbers } from "@/redux/features/BarberSlice";
import { useRouter } from "next/navigation";
import { ChevronLeft, ChevronRight } from "lucide-react";

interface Barber {
  _id: string;
  employeeName: string;
  employeeFees: number;
  specialCut: string;
  description?: string;
  employeeImage?: string;
}

const Bookinglist = () => {
  const dispatch: AppDispatch = useDispatch();
  const { service, loading: loadingServices, error: serviceError } = useSelector((state: RootState) => state.service);
  const { barbers, loading: loadingBarbers, error: barberError } = useSelector((state: RootState) => state.barber);
  const scrollServiceRef = useRef<HTMLDivElement | null>(null);
  const scrollBarberRef = useRef<HTMLDivElement | null>(null);
  const router = useRouter();

  useEffect(() => {
    dispatch(fetchServices());
    dispatch(fetchBarbers());
  }, [dispatch]);

  const scroll = (direction: "left" | "right", target: "service" | "barber") => {
    const ref = target === "service" ? scrollServiceRef.current : scrollBarberRef.current;
    if (ref) {
      ref.scrollBy({
        left: direction === "left" ? -300 : 300,
        behavior: "smooth",
      });
    }
  };

  return (
    <div className="text-[#D6D7D6] px-6 py-12 space-y-24 bg-[#1B1B1A] min-h-screen font-sans">

      {/* ------------------ Services Section ------------------ */}
      <section>
        <h2 className="text-4xl font-bold text-center text-[#D6D7D6] mb-2">Salon Services</h2>
        <p className="text-sm text-center text-[#9ca3af] mb-6">Premium grooming, curated for you.</p>

        <div className="relative group">
          <button
            onClick={() => scroll("left", "service")}
            className="absolute z-40 left-0 top-1/2 transform -translate-y-1/2 bg-[#2B2B2B] hover:bg-[#757442] text-white p-2 rounded-full shadow-lg opacity-0 group-hover:opacity-100 transition duration-300"
          >
            <ChevronLeft size={20} />
          </button>

          <div
            ref={scrollServiceRef}
            className="overflow-x-auto no-scrollbar whitespace-nowrap flex gap-5 px-2 py-4 scroll-smooth"
          >
            {loadingServices ? (
              <p className="text-gray-400">Loading...</p>
            ) : serviceError ? (
              <p className="text-red-500">Error loading services</p>
            ) : (
              service.map((item: any, index: number) => (
                <div
                  key={index}
                  className="w-[220px] h-[210px] bg-[#1B1B1A]/90 border border-[#757442]/20 rounded-xl flex-shrink-0 p-4 text-center backdrop-blur-sm cursor-pointer hover:scale-105 hover:shadow-[0_0_12px_#75744266] transition-all duration-300"
                >
                  <img
                    src={item.image}
                    alt={item.serviceName}
                    className="w-full h-[100px] object-cover rounded-md mb-3"
                  />
                  <h4 className="text-lg font-medium truncate">{item.serviceName}</h4>
                  <p className="text-sm text-[#A3A3A3] mt-1">₹ {item.serviceCharge}</p>
                </div>
              ))
            )}
          </div>

          <button
            onClick={() => scroll("right", "service")}
            className="absolute z-40 right-0 top-1/2 transform -translate-y-1/2 bg-[#2B2B2B] hover:bg-[#757442] text-white p-2 rounded-full shadow-lg opacity-0 group-hover:opacity-100 transition duration-300"
          >
            <ChevronRight size={20} />
          </button>
        </div>
      </section>

      {/* ------------------ Barbers Section ------------------ */}
      <section>
        <h2 className="text-4xl font-bold text-center text-[#D6D7D6] mb-2">Our Barbers</h2>
        <p className="text-sm text-center text-[#9ca3af] mb-6">Pick your artist — each cut is crafted with care.</p>

        <div className="relative group">
          <button
            onClick={() => scroll("left", "barber")}
            className="absolute z-40 left-0 top-1/2 transform -translate-y-1/2 bg-[#2B2B2B] hover:bg-[#757442] text-white p-2 rounded-full shadow-lg opacity-0 group-hover:opacity-100 transition duration-300"
          >
            <ChevronLeft size={20} />
          </button>

          <div
            ref={scrollBarberRef}
            className="overflow-x-auto no-scrollbar whitespace-nowrap flex gap-5 px-2 py-4 scroll-smooth"
          >
            {loadingBarbers ? (
              <p className="text-gray-400">Loading barbers...</p>
            ) : barberError ? (
              <p className="text-red-500">Error loading barbers</p>
            ) : (
              barbers.map((barber: Barber) => (
                <div key={barber._id} className="flex-shrink-0 w-[200px]">
                  <div
                    onClick={() => router.push(`/admin/barbers/${barber._id}/barberprofileview`)}
                    className="p-4 rounded-2xl shadow-lg bg-[#2B2B2B]/90 border border-[#757442]/30 hover:shadow-[#757442]/40 hover:scale-105 transition-all duration-300 cursor-pointer backdrop-blur-md text-center"
                  >
                    <div className="w-full h-[130px] overflow-hidden rounded-xl mb-3 border border-[#757442]/20">
                      <img
                        src={barber.employeeImage || "/placeholder.png"}
                        alt={barber.employeeName}
                        className="object-cover w-full h-full hover:scale-110 transition-transform duration-300"
                      />
                    </div>
                    <div className="space-y-1">
                      <h6 className="text-md font-semibold truncate">{barber.employeeName}</h6>
                      <p className="text-sm italic text-[#757442] truncate">{barber.specialCut}</p>
                      <p className="text-sm font-bold">₹ {barber.employeeFees}</p>
                      <p className="text-xs text-gray-400 line-clamp-2 h-10">{barber.description}</p>
                    </div>
                  </div>
                </div>
              ))
            )}
          </div>

          <button
            onClick={() => scroll("right", "barber")}
            className="absolute z-40 right-0 top-1/2 transform -translate-y-1/2 bg-[#2B2B2B] hover:bg-[#757442] text-white p-2 rounded-full shadow-lg opacity-0 group-hover:opacity-100 transition duration-300"
          >
            <ChevronRight size={20} />
          </button>
        </div>
      </section>
    </div>
  );
};

export default Bookinglist;

